body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

body{
  background-color:  #81baec;
  font-family: 'lato';
  background-image: url(/shoppingReactjs/static/media/back.03f46279.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  width: auto;
}
.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  align-items: center;
  border-radius: 10px;
  
}

#table{
    padding: 30px;
    padding-top: 30px;
    border-style: solid black;
    width: 1000px;
    background-color: rgb(226, 226, 245);
    border-radius: 10px;
    opacity: 0.9;
    /* margin-top: 20%; */

}
#table td{
    padding-left: 30px;
    margin-bottom: 10%;
    padding-bottom: 5%;
    
}

img{
   height: 80px;

}
.btn{
    margin-top: 100px;
    padding: 10px;
    border-radius: 10px;
    font-weight: bold;
    background-color: cornflowerblue;
}
