.transaction-item{
    /* display: grid; 
    grid-template-columns: 4fr 0fr 1fr; */
    color: black;
    width: 950px;
    height: auto;
    margin-top: 5px;
    border-radius: 10px;
    background-color: rgb(248, 244, 244);
    border:  solid 1px rgb(245, 240, 240);
    opacity: 0.9;
    padding-left: 50px;
    padding-right: 30px;
    padding-bottom: 100px;

      
}
.head{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bolder;
    color: red;
    font-size: xx-large;
    margin-bottom: 5%;
    text-align: center;
}
.name{
    font-weight: bolder;
    color: red;
}
.price{
    justify-content: center;
    text-align: right;
    margin-top: -2%;
    margin-right: 30%;
    font-weight: bolder;
    color: red;
}
.amount{
    text-align: right;
    margin-right: 30%;
    margin-top: -3%;

}
.vertical {
    border-left: 6px solid black;
    content: '';
    width: 0;
    height: auto;
    position:absolute;
    left: 50%;
}
